<template>
  <main class="property-parent">
    <b-container>
      <b-row v-if="property_parent">
        <b-col cols="12" class="py-2">
          <h1>
            <span>
              {{ property_parent[`name_${$i18n.locale}`] }}
            </span>
            <b-avatar
              v-if="property_parent.type_en == 'Hotel'"
              :src="avatar"
            ></b-avatar>
          </h1>
          <StarRateBox
            v-if="property_parent.type_en == 'Hotel'"
            :showReview="false"
            :with-star-rating="true"
            :rate="property_parent.hotel_stars"
            class="star-rate-box"
          />
        </b-col>
        <b-col cols="12" class="mx-auto" style="margin-top: 20px">
          <b-row>
            <b-col cols="12">
              <swiper :options="swiperOptionThumbs" ref="swiper_ref">
                <swiper-slide
                  v-for="(img, index) in categoryImages[0].images"
                  :key="index"
                  class="img-wrapper"
                >
                  <AppImage
                    :src="img"
                    :class="(index + 1) % 2 == 0 ? 'active-img' : ''"
                  />
                </swiper-slide>

                <div class="swiper-button-prev" slot="button-prev"></div>
                <div class="swiper-button-next" slot="button-next"></div>
              </swiper>
            </b-col>
          </b-row>
          <b-row style="margin-top: 20px">
            <b-col
              cols="12"
              lg="4"
              v-for="(category, key, index) in categories"
              :key="index"
              @click="getCategoryImage(category)"
            >
              <h5
                class="text-center"
                :style="{
                  color: category === singleCategory ? '#0080fd' : '#444',
                  cursor: 'pointer',
                }"
              >
                {{ category }}
              </h5>
            </b-col>
          </b-row>
        </b-col>
        <b-col cols="12" class="mt-4" v-if="property_parent.type_en == 'Hotel'">
          <h4>{{ $t('common.property_description') }}</h4>
          <p>
            {{ property_parent[`description_${$i18n.locale}`] }}
          </p>
        </b-col>
        <b-col cols="12" class="mt-2" v-if="property_parent.type_en == 'Hotel'">
          <h4>{{ $t('common.facilities') }}</h4>
          <div
            v-for="(amenity, key, index) in property_parent.facilities"
            :key="index"
          >
            <h4>{{ key }}</h4>
            <ul class="px-0">
              <li v-for="(item, i) in amenity" :key="i">
                <strong>{{ item.category }}</strong>
              </li>
            </ul>
          </div>
        </b-col>
        <b-col cols="12" class="mt-2" v-if="property_parent.type_en == 'Hotel'">
          <h4>{{ $t('common.payment_policy') }}</h4>
          <p>{{ property_parent.payment_policy }}</p>
        </b-col>
        <b-col cols="12" class="mt-2" v-if="property_parent.type_en == 'Hotel'">
          <h4>{{ $t('common.house_rules') }}</h4>
          <p>{{ property_parent.house_rules }}</p>
        </b-col>
        <b-col cols="12" class="mt-4">
          <h4>{{ $t('common.location') }}</h4>
        </b-col>

        <b-col cols="12" class="mb-4">
          <div>
            <GoogleMap
              :markerLabel="property_parent[`name_${$i18n.locale}`]"
              :location="{
                lat: property_parent.lat,
                lng: property_parent.lon,
              }"
            />
          </div>
        </b-col>
        <b-col cols="12" class="mb-4">
          <h4>
            {{ $t('common.listing_located') }}
            <span v-if="property_parent.type_en == 'Hotel'">
              {{ $t('common.hotel') }}
            </span>
            <span v-else>
              {{ property_parent[`name_${$i18n.locale}`] }}
            </span>
          </h4>
        </b-col>
      </b-row>
      <b-row v-if="properties">
        <b-col
          cols="12"
          lg="4"
          v-for="(property, index) in properties"
          :key="index"
        >
          <PropertyCard
            :hideActions="true"
            :cardData="property"
            @showSendOfferModal="handleOffer"
            @updateFavouriteProperty="updateFavourite"
          />
        </b-col>
      </b-row>
    </b-container>
  </main>
</template>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import StarRateBox from '@/components/shared/StarRateBox'
import { generateServerImageUrl } from '@/helpers/images'
import GoogleMap from '@/components/shared/CustomGoogleMap.vue'
import PropertyCard from '@/components/shared/PropertyCard'
import { createNamespacedHelpers } from 'vuex'
const { mapActions } = createNamespacedHelpers('PropertyModule')
export default {
  name: 'PropertyParent',
  components: { Swiper, SwiperSlide, StarRateBox, GoogleMap, PropertyCard },

  async asyncData({ $axios, params, query }) {
    const res = await $axios.get(`/getSingleProperty/${params.id}`, {
      params: query,
    })
    const { Data } = res.data

    const parentProperties = await $axios.get(
      `/property-parents/${Data.property_parent.id}/properties`
    )

    const categories = Object.keys(Data.property_parent.images)
    const singleCategory = categories && categories[0]

    return {
      property_parent: Data.property_parent,
      categories,
      singleCategory,
      categoryImages: Data.property_parent.images[singleCategory],
      properties: parentProperties.data.Data.properties,
    }
  },
  data() {
    return {
      swiperOptionThumbs: {
        slidesPerView: 'auto',
        loopedSlides: 0,
        loop: false,
        touchRatio: 0.2,
        slideToClickedSlide: false,
        observer: true,
        observeParents: true,
        spaceBetween: 10,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        breakpoints: {
          1200: {
            slidesPerView: 3,
            spaceBetween: 10,
          },
          900: {
            slidesPerView: 2,
            spaceBetween: 10,
          },
          450: {
            slidesPerView: 1,
            spaceBetween: 10,
          },
        },
      },
      settings: {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 4,
        initialSlide: 0,
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 3,
              infinite: true,
              dots: true,
            },
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2,
            },
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
            },
          },
        ],
        pauseOnHover: true,
        autoplay: true,
        lazyLoad: 'ondemand',
      },
    }
  },
  computed: {
    avatar() {
      generateServerImageUrl({
        $config: this.$config,
        url: this.property_parent.logo,
      })
    },
  },

  methods: {
    ...mapActions(['updatePropertyFavourite', 'showSendOfferModal']),
    handleOffer(offer) {
      this.showSendOfferModal({ offerData: offer })
    },
    updateFavourite(data) {
      let formdata = {
        prop_id: data.property_id,
        term: this.$route.query.term,
        type: 'search-property',
      }
      this.updatePropertyFavourite(formdata)
    },
    getCategoryImage(category) {
      this.singleCategory = category
    },
  },
  watch: {
    singleCategory: {
      handler(value) {
        if (value) this.categoryImages = this.property_parent.images[value]
      },
      immediate: true,
    },
  },
}
</script> 

<style scoped lang="scss">
.property-parent {
  .slick-slider {
    /* display: flex; */
  }
  .custom-arrow {
    &:before {
      display: none !important;
    }
    height: 20px;
  }

  .slider-wrapper {
    height: 400px;
  }
  .img-wrapper {
    img {
      width: 100%;
      height: 350px;
      /* border-radius: 8px !important; */
      transform: scale(0.8);

      @include media-breakpoint-down(sm) {
        height: 200px;
      }
    }
  }
  .active-img {
    transform: scale(1.1) !important;
  }
}
ul {
  list-style: initial !important;
}
</style>