<template>
  <nuxt-link
    :to="{
      path: !isHome
        ? localePath('/rent/' + cardData.property_slug)
        : localePath('/Dahab'),
      query: { term: term },
    }"
    :class="isThankYouPage && 'thankyou-card'"
  >
    <div class="property-card">
      <div class="image-wrapper">
        <AppImage
          :src="cardData.property_image[0]"
          :height="202"
          :quality="50"
          v-if="typeof cardData.property_image[0] === 'string'"
          class="object-fit-cover bg-dark"
        />
      </div>
      <div class="property-data">
        <div class="favorite">
          <b-button
            style="background-color: transparent"
            variant="link"
            class="side-btn favourite-button"
            @click="
              updateFavourite($event, cardData.property_id, cardData.term)
            "
          >
            <svg-icon
              v-bind:name="
                cardData.isFav ? 'grey-checked-heart' : 'grey-unchecked-heart'
              "
              fill="#204ECF"
              width="24px"
              height="24px"
            />
          </b-button>
        </div>

        <div class="property-card-details">
          <h3 class="location-details mb-2 alignment property-name">
            {{ cardData.property_name | displayExcerpt(40) }}
          </h3>
          <section>
            <div class="d-flex justify-content-between flex-column">
              <section class="actions-container">
                <div class="action-item">
                  <svg-icon name="grey-bed" fill="#0F256E" />
                  <span class="action-text"
                    >{{ cardData.no_of_bedroom }}Bed</span
                  >
                </div>
                <div class="action-item">
                  <svg-icon name="grey-bath" fill="#0F256E" />
                  <span class="action-text"
                    >{{ cardData.no_bathrooms }}Bath</span
                  >
                </div>
                <div class="action-item">
                  <svg-icon name="grey-area" fill="#0F256E" />
                  <span class="action-text">{{ cardData.property_area }}M</span>
                  <span class="area-unit">&#178;</span>
                </div>
              </section>
              <div class="price m-0 d-flex" style="font-size: 16px">
                <span
                  style="
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                  "
                >
                </span>
                <div :class="isThankYouPage && 'mt-2'">
                  <span class="property-price">
                    {{
                      longTerm
                        ? `$ ${cardData.min_monthly_price} ${$t(
                            'common.egp'
                          )} - ${cardData.max_monthly_price} ${$t(
                            'common.egp'
                          )}`
                        : ` ${cardData.min_daily_price} ${$t('common.egp')} - ${
                            cardData.max_daily_price
                          } ${$t('common.egp')}`
                    }}
                  </span>
                  <span class="font-size-smaller">
                    {{
                      longTerm
                        ? '/ ' + $t('common.month')
                        : '/' + $t('common.night')
                    }}
                  </span>
                </div>
              </div>
            </div>
          </section>
        </div>

        <b-button
          style="border: 0; background-color: transparent"
          v-if="!hideActions && !isThankYouPage"
          class="compare-button compare-step"
          @click="sendProperty($event, cardData.property_id)"
          v-b-tooltip.hover.bottom="$t('property.compareTooltip')"
        >
          <span style="color: #0b86e0; font-size: 14px">
            {{ $t('property.compare') }}
          </span>
        </b-button>
      </div>
    </div>
  </nuxt-link>
</template>

<script>
import StarRateBox from '@/components/shared/StarRateBox.vue'
import { generateServerImageUrl } from '@/helpers/images'
import { createNamespacedHelpers } from 'vuex'
const { mapState } = createNamespacedHelpers('PropertyModule')
export default {
  name: 'PropertyCard',
  components: {
    StarRateBox,
  },
  props: ['cardData', 'hideActions', 'hideCompare', 'isHome', 'isThankYouPage'],
  data() {
    return {
      demoImg: 'https://picsum.photos/1024/480/?image=10',
      rating: null,
      lastSlideIndexLoaded: 0,
      generateServerImageUrl,
      form: {
        offer_price: 0,
      },
    }
  },
  computed: {
    ...mapState({
      termState: (state) => state.term,
      visibility: (state) => state.confirmationModal.visibility,
    }),
    term() {
      let cardTerm = this.cardData.favourite_term == 1 ? 1 : 0
      let term = this.$route.query.term ? this.termState : cardTerm
      return term
    },
    longTerm() {
      return this.term == 1
    },
    shortTerm() {
      return this.term == 0
    },
    bidId() {
      return this.$store.state.PropertyModule.bidId
    },
    propertyRating() {
      return this.cardData.property_rating.toFixed(1)
    },
  },
  methods: {
    gtag_report_conversion(url) {
      var callback = function () {
        if (typeof url != 'undefined') {
          window.location = url
        }
      }
      gtag('event', 'conversion', {
        send_to: 'AW-10826966991/6eLyCJrx54sDEM_P2aoo',
        event_callback: callback,
      })
      return false
    },
    sendProperty(e, id) {
      e.preventDefault()
      this.$emit('update', { id, term: this.term })
    },
    sendOffer(cardData) {
      const formData = { ...cardData, ...this.form }

      this.$emit('showSendOfferModal', formData)
      this.gtag_report_conversion()
    },
    updateFavourite(e, property_id) {
      e.preventDefault()
      this.$emit('updateFavouriteProperty', {
        property_id: property_id,
        term: this.term,
      })
    },
    onCarouselSlidingStart(slide) {
      this.lastSlideIndexLoaded =
        slide > this.lastSlideIndexLoaded ? slide : this.lastSlideIndexLoaded
    },
    goToPropertyDetails() {
      this.$router.push(
        '/rent/' + this.cardData.property_slug + '#property-reviews-section'
      )
    },
  },
  watch: {
    bidId(newVal) {
      if (newVal == this.cardData.property_id) {
        this.$refs.offerBtn.click()
      }
    },
    visibility: {
      handler(value) {
        if (!value) {
          this.form.offer_price = 0
        }
      },
      immediate: true,
    },
  },
}
</script>
<style lang="scss">
.property-card {
  .input-postfix {
    font-size: 12px !important;
  }
}
</style>
<style lang="scss" scoped>
.property-card {
  margin-bottom: 20px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 10px;
  border-bottom: 1px solid $grayLightColor;
  .image-wrapper {
    img {
      width: 86px;
      height: 86px;
    }
  }
  .property-data {
    .favorite {
      position: absolute;
      top: 0;
      right: 0;
      .btn {
        padding: 0;
      }
    }
    .compare-button.btn {
      background-color: #fff !important;
      padding: 5px 0 !important;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      color: #204ecf;
      border-radius: 8px;
      border: 1px solid $grayLightColor;
    }
  }
  .property-card-details {
    .btn {
      text-transform: capitalize;
    }
    svg {
      width: 18px;
      height: 18px;
    }
    p {
      font-size: 12px;
    }
    p.price {
      font-size: 18px;
      font-weight: 800;
    }
    .location-details {
      font-size: 14px;
      color: $mainTextColor;
    }
    .property-name {
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      display: flex;
      align-items: center;
      color: #0f256e;
    }
    .property-price {
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      color: #262d3d;
    }
  }
  .rating-box {
    cursor: pointer;
  }
  .actions-container {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    gap: 10px;
    align-items: baseline;
    .action-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 8px 12px;
      gap: 2px;
      height: 30px;
      background: linear-gradient(
          0deg,
          rgba(38, 45, 61, 0.04),
          rgba(38, 45, 61, 0.04)
        ),
        #ffffff;
      border-radius: 8px;
      .action-text {
        color: #0f256e;
        font-weight: 400;
        font-size: 12px;
      }
      .area-unit {
        text-transform: uppercase;
        color: inherit;
      }
    }
  }
  @include media-breakpoint-down(sm) {
    padding-bottom: 15px;
  }
}

//laptop

@media only screen and (min-width: 1024px) {
  .property-card {
    flex-direction: column;
    justify-content: flex-start;
    border: none;
    .image-wrapper {
      img {
        width: 100%;
        height: 197px;
      }
    }
    .property-data {
      .favorite {
        right: 20px;
        background-color: white;
        top: 5px;
        height: 32px;
        width: 32px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
        padding: 4px;
        button {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .thankyou-card {
    display: block;
    background-color: white;
    border-radius: 4px;
    .image-wrapper img {
      border-radius: 4px;
    }
    .property-data {
      padding: 10px 10px;
    }
  }
}
</style>
